.flex-search-holder{
    display:flex;
    flex-direction: row;
    justify-content: space-between;
    align-items:center;
    flex-wrap:wrap;
    box-shadow:3px 3px #888888;
    border: 1px black solid;
    margin-bottom:25px;
    height: 75px;
    margin:25px;
    padding:20px;
    position: sticky;
    top: 0;
    background-color:white;
}

.search-form{
    margin-right:10px;
    display:flex;
    flex-direction:row;
    border-bottom: 1px solid #000000;

}

.welcome-holder{
    margin-left:10px;
    cursor:pointer;
}

.search-button{
    background:transparent;
    border: none;
    outline:none;
    cursor:pointer;
}

.search-form input[type=text] {
    background: transparent;
    border: none;
    padding: 2px 5px;
    outline:none;
}

.button-image{
    height: 20px;
    width:20px;


}

